.banner-container {
  margin: 0;
  padding: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}

.brand-container {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.logo-banner {
  width: 14rem;
  padding-bottom: 5rem;
}

.title-banner {
  font-family: Nunito Sans;
  font-weight: bold;
  font-size: 3rem;
  color: black;
  padding-bottom: 2rem;
}

.subtitle-banner {
  font-family: Nunito Sans, Semibold;
  font-size: 2rem;
  text-align: center;
  width: 85%;
  padding-bottom: 6rem;
}

.products-button-banner {
  cursor: pointer;
  outline: none;
  font-family: Nunito Sans;
  font-weight: 600;
  font-size: 2rem;
