.about-us {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
}

.title-container-about-us {
  display: flex;
  height: 10rem;
  width: 100%;
  align-items: center;
  position: relative;
  margin-top: 5rem;
}

.background-about-us {
  background-color: #E9E9E9;
  box-shadow: 15px 15px 15px #00000029;
  position: relative;
  opacity: 1;
  display: flex;
  width: 27rem;
  height: 5rem;
  justify-content: center;
  align-items: center;
}

.title-about-us {
  font-family: Nunito Sans;
  font-size: 3.5rem;
  font-weight: bold;
  color: black;
  position: absolute;                                                                                                                    
  left: 3.5rem;
  z-index: 10;
  top: 0.1rem;
}

.subtitle-about-us {
  font-family: Nunito Sans;
  font-size: 1.5rem;
  position: absolute;
  margin: 0;
}

.owner-picture {
  align-self: center;
  object-fit: cover;
  margin-top: 3rem;
  margin-bottom: 3rem;
  width: 35rem;
  height: 30rem;
  box-shadow: 15px 15px 15px #00000029;
}

.information-container-about-us {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.about-us-text {
  font-family: Nunito Sans;
  font-size: 2rem;
  width: 40rem;
  flex-wrap: wrap;
  margin-top: 3rem;
  margin-bottom: 3rem;
}

.values-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.each-value {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 4rem;
}

.values-icons {
  width: 15rem;
}

.values-text {
  font-family: Nunito Sans;
  width: 26rem;
  flex-wrap: wrap;
  font-size: 2rem;
  text-align: center;
}

@media(min-width: 1024px) {
