body, html, header{
    margin: 0;
    padding: 0;
    font-size: 62.5%;
}

html {
    scroll-behavior: smooth;
}
.icons-menu {
    display: none;
}
.nav-bar {
    width: 100vw;

    position: fixed;
    top: 0;

    display: flex;
    justify-content: space-around;
    align-items: center;

    background-color: #000000;

    z-index: 20;
}

.nav-bar-menu {
    display: flex;

    font: 600 1.6rem Nunito Sans;
    
    text-transform: uppercase;

    list-style: none;
}

.nav-bar-menu li {
    padding: 1rem;
}

.nav-bar-menu li a {
    text-decoration: none;
    color: #CBCBCB;
}

.nav-bar-menu li a:hover {
    opacity: 0.8;
}



@media (max-width: 768px) {
    html {
        font-size: 50%;
    }
    nav {
        display: none;
    }
    .nav-bar {
        justify-content: space-between;
        height: 10vh;
    }
    .logo-header {
        width: 60px;
        margin-left: 15px;
    }
    .one,
    .two,
    .three {
        background-color: #CBCBCB;
        height: 4px;
        width: 100%;
        margin: 6px auto;

        transition-duration: 0.3s;
    }
    .menu-session.on .icons-menu {
        display: block;
        position: absolute;
        bottom: 10%;
    }
    .menu-session.on .icons-menu a {
        padding: 5px;
    }
    .menu-toggle {
        width: 35px;
        height: 30px;
        
        margin-right: 15px;
    }
    .menu-session.on {
        width: 70vw;
        height: 100vh;

        position: absolute;
        top: 0;
        right: 0;

        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;

        z-index: 10;

        background-color: #000000;
        opacity: 0.9;

        transition-duration: 0.5s;
    }
    .menu-session.on nav {
        display: block;
    }
    .menu-session.on .menu-toggle {
        position: absolute;
        top: 2%;
        left: 0;
        margin-left: 15px;
        transition-duration: 0.8s;
    }
    .menu-session.on .three {
        width: 70%;
        margin-left: 0;
    }
    .menu-session.on .nav-bar-menu {
        text-align: center;
        display: block;
        padding: 0;
    }
    .menu-session.on .nav-bar-menu a {
        transition: 0.5s;
        font-size: medium;
    }
    .menu-session.on .nav-bar-menu li {
        padding: 3rem;
    }
}
