.carrossel-dots{
    position: absolute;
    bottom: -100px;
    display: block;
    width: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
    text-align: center;
}

.carrossel-dots li {
    position: relative;
    display: inline-block;
    width: 25px;
    height: 20px;
